.form {
  margin: 0 auto;
  padding: 0;
  //box-shadow: inset 0 0 0 4px rgb(34, 252, 5);
}

.form__wrapper {
  margin: 0 auto;
  padding: 0;
}

.form__introduction {
  font-size: 14px;
  font-weight: 400;
  line-height: 2.14;
  text-align: center;
  color: $dark;
  padding-left: 20px;
  padding-right: 20px;
}

.form__introduction-add {
  display: none;
}

.form__input {
  font-size: 14px;
  font-weight: 400;
  line-height: 2.14;
  text-align: left;
  padding-left: 21px;
  color: $pink;
  text-transform: uppercase;
}

.form__label-wrapper {
  display: block;
  width: 276px;
  margin: 0 auto;
  padding: 0;
  //box-shadow: inset 0 0 0 4px rgb(34, 252, 5);
}

.form__label-wrapper--emotions {
  text-align: center;
}

.form__input--emotions {
  padding-left: 0;
}

.form__input-value {
  width: 276px;
  min-height: 59px;
  box-sizing: border-box;
  border: solid 2px #e5e5e5;
}

.form__input-wrapper {
  text-align: center;
}

.form__input-value:hover {
  border: solid 2px $dark;
}

.form__input-value:active {
  border: solid 2px $pink;
}

.form__input-value[type="text"]::placeholder {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
  text-align: left;
  color: $dark;
  padding-left: 20px;
  white-space: pre-wrap;
  //padding-left: 13px;
  //-webkit-box-sizing: border-box;
  //box-sizing: border-box;
}

.form__input-value[type="tel"]::placeholder {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
  text-align: left;
  color: $dark;
  padding-left: 20px;
  //padding-left: 13px;
  //-webkit-box-sizing: border-box;
  //box-sizing: border-box;
}

.form__input-value[type="email"]::placeholder {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
  text-align: left;
  color: $dark;
  padding-left: 20px;
  //padding-left: 13px;
  //-webkit-box-sizing: border-box;
  //box-sizing: border-box;
}

.form__input-value--emotions {
  min-height: 197px;
}

.form__legend {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  text-align: center;
  color: $pink;
  text-transform: uppercase;
  padding-left: 30px;
  padding-right: 30px;
}

.form__answers {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
  text-align: left;
  color: $dark;
  cursor: pointer;
}

.form__answer {
  position: absolute;
  opacity: 0;
}

.form__answer + .form__answer-new {
  position: relative;
  padding: 0 0 0 35px;
}

.form__answer + .form__answer-new::before {
  content: "";
  position: absolute;
  top: -6px;
  left: -33px;
  width: 35px;
  height: 35px;
  border: solid 2px #e5e5e5;
  background-color: #ffffff;
}

.form__answer + .form__answer-new::after {
  content: "";
  position: absolute;
  top: 2px;
  left: -25px;
  width: 22px;
  height: 16px;
  opacity: 0;
  transition: all 0.2s;
  background-image: url("../img/icon-yes.svg");
}

.form__answer:checked + .form__answer-new::after {
  opacity: 1;
}

.form__answer:checked + .form__answer-new::before {
  opacity: 1;
}

.form__answer:hover + .form__answer-new::before {
  border-color: $dark;
}

.form__answer:active + .form__answer-new::before {
  border-color: $pink;
}

.form__answer-os {
  position: absolute;
  opacity: 0;
}

.form__answer-os + .form__answer-os-new {
  position: relative;
  padding: 0 0 0 35px;
}

.form__answer-os + .form__answer-os-new::before {
  content: "";
  position: absolute;
  top: -7px;
  left: -34px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: solid 2px #e5e5e5;
  background-color: #ffffff;
}

.form__answer-os + .form__answer-os-new::after {
  content: "";
  position: absolute;
  top: 4px;
  left: -23px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: solid 1px $pink;
  background-color: $pink;
  opacity: 0;
  transition: all 0.2s;
}

.form__answer-os:checked + .form__answer-os-new::after {
  opacity: 1;
}

.form__answer-os:checked + .form__answer-os-new::before {
  opacity: 1;
}

.form__answer-os:hover + .form__answer-os-new::before {
  border-color: $dark;
}

.form__answer-os:active + .form__answer-os-new::before {
  border-color: $pink;
}

.form__button {
  width: 276px;
  min-height: 59px;
  border: solid 2px #2aab6d;
  background-color: #2aab6d;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.67;
  text-align: center;
  color: #ffffff;
  //margin: 0 auto;
}

.form__button-wrapper {
  min-width: 276px;
  margin: 0 auto;
  //box-shadow: inset 0 0 0 19px rgb(252, 63, 5);
}

.form__button:hover {
  border: solid 2px #1c9b5e;
  background-color: #1c9b5e;
}

.form__button:active {
  border: solid 2px #1c9b5e;
  background-color: #1c9b5e;
  color: rgba(255, 255, 255, 0.3);
}

.form__button:disabled {
  border: solid 2px #e5e5e5;
  background-color: #e5e5e5;
  color: #ffffff;
}

.form__notice {
  font-size: 14px;
  font-weight: 400;
  line-height: 2.14;
  text-align: center;
  color: $dark;
}

.form__notice-star {
  color: $pink;
}

.form__popup {
  width: 320px;
  margin: 0 auto;
  display: none;
  //box-shadow: inset 0 0 0 3px rgb(252, 5, 240);
}

.form__popup-title {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  color: #000000;
  padding-top: 44px;
}

.form__popup-text {
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  color: $dark;
  padding-left: 23px;
  padding-right: 23px;
  padding-bottom: 25px;
}

.form__button-popup {
  margin-bottom: 52px;
}

.form__button-success-wrapper {
  padding-top: 52px;
  padding-left: 22px;
  padding-right: 22px;
  margin: 0 auto;
  background-color: #f2f2f2;
  //box-shadow: inset 0 0 0 3px rgb(252, 5, 240);
}

.form__list {
  list-style: none;
  text-align: left;
  max-width: 320px;
  margin: 0 auto;
  padding-left: 42px;
}

.form__fieldset {
  border: none;
}

.form__answer-wrapper {
  padding-bottom: 47px;
}

.form__answer-os-wrapper {
  padding-bottom: 41px;
}

